// components/dynamic-zone/how-it-works/card.tsx
'use client';

import React from 'react';
import Link from 'next/link';
import { motion, useReducedMotion } from 'framer-motion';
import { ArrowRight, type LucideIcon } from 'lucide-react';

export const Card = ({
  title,
  description,
  index,
  badge,
  icon: Icon,
  ctaText,
  ctaHref,
  desktopLayout,
}: {
  title: string;
  description: string;
  index: number;
  badge: string;
  icon: LucideIcon;
  ctaText?: string;
  ctaHref?: string;
  desktopLayout: 'content-left' | 'content-right';
}) => {
  const reduce = useReducedMotion();

  const item = {
    hidden: { opacity: 0, y: reduce ? 0 : 10 },
    show: {
      opacity: 1,
      y: 0,
      transition: reduce ? { duration: 0.2 } : { duration: 0.55, ease: [0.22, 1, 0.36, 1] },
    },
  };

  const node = {
    hidden: { scale: reduce ? 1 : 0.98, opacity: 0.9 },
    show: {
      scale: 1,
      opacity: 1,
      transition: reduce ? { duration: 0.2 } : { duration: 0.45, ease: [0.22, 1, 0.36, 1] },
    },
  };

  const DesktopContent = () => (
    <div className="max-w-xl">
      <h3 className="text-xl md:text-2xl font-semibold tracking-tight text-[var(--lightblack)]">{title}</h3>
      <p className="mt-3 text-sm md:text-base leading-relaxed text-[var(--secondary)]">{description}</p>

      {ctaText && ctaHref ? (
        <div className="mt-4">
          <Link
            href={ctaHref}
            className="inline-flex items-center gap-2 text-sm font-medium text-[var(--muted)] underline-offset-4 hover:underline focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[var(--muted)] focus-visible:ring-offset-2 focus-visible:ring-offset-[var(--charcoal)]"
            aria-label={ctaText}
          >
            <span>{ctaText}</span>
            <ArrowRight className="h-4 w-4" />
          </Link>
        </div>
      ) : null}
    </div>
  );

  const DesktopMeta = ({ align }: { align: 'start' | 'end' }) => (
    <div className={align === 'end' ? 'md:justify-self-end' : 'md:justify-self-start'}>
      <div className={`flex flex-col gap-3 ${align === 'end' ? 'items-end' : 'items-start'}`}>
        <Icon className="h-5 w-5 text-[var(--muted)]" aria-hidden />
        <span className="text-[10px] tracking-[0.22em] uppercase text-[var(--muted)]">{badge}</span>
      </div>
    </div>
  );

  return (
    <motion.li variants={item} className="relative">
      {/* Mobile (2 cols: timeline | content) */}
      <div className="grid grid-cols-[40px_1fr] gap-6 md:hidden">
        {/* Node */}
        <div className="relative flex items-start justify-center">
          <motion.div
            variants={node}
            initial="hidden"
            whileInView="show"
            viewport={{ once: true, amount: 0.55 }}
            className="mt-1 flex h-10 w-10 items-center justify-center rounded-full border border-[var(--muted)] bg-[var(--charcoal)]"
            style={{ borderWidth: 1, borderColor: 'var(--muted)' }}
          >
            <span className="text-xs font-semibold text-[var(--muted)]">{index}</span>
          </motion.div>
        </div>

        {/* Content + Meta inline */}
        <div className="min-w-0">
          <div className="flex items-center gap-2 text-[var(--muted)]">
            <Icon className="h-4 w-4" aria-hidden />
            <span className="text-[10px] tracking-[0.22em] uppercase">{badge}</span>
          </div>

          <h3 className="mt-3 text-lg font-semibold tracking-tight text-[var(--lightblack)]">{title}</h3>
          <p className="mt-2 text-sm leading-relaxed text-[var(--secondary)]">{description}</p>

          {ctaText && ctaHref ? (
            <div className="mt-3">
              <Link
                href={ctaHref}
                className="inline-flex items-center gap-2 text-sm font-medium text-[var(--muted)] underline-offset-4 hover:underline focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[var(--muted)] focus-visible:ring-offset-2 focus-visible:ring-offset-[var(--charcoal)]"
                aria-label={ctaText}
              >
                <span>{ctaText}</span>
                <ArrowRight className="h-4 w-4" />
              </Link>
            </div>
          ) : null}
        </div>
      </div>

      {/* Desktop (3 cols: left | timeline | right) */}
      <div className="hidden md:grid md:grid-cols-[1fr_72px_1fr] md:gap-10 md:py-2">
        {/* Left */}
        <div className="min-w-0">
          {desktopLayout === 'content-left' ? (
            <DesktopContent />
          ) : (
            <DesktopMeta align="end" />
          )}
        </div>

        {/* Timeline node (center col) */}
        <div className="relative flex items-start justify-center">
          <motion.div
            variants={node}
            initial="hidden"
            whileInView="show"
            viewport={{ once: true, amount: 0.6 }}
            className="mt-1 flex h-12 w-12 items-center justify-center rounded-full border border-[var(--muted)] bg-[var(--charcoal)]"
            style={{ borderWidth: 1, borderColor: 'var(--muted)' }}
          >
            <span className="text-sm font-semibold text-[var(--muted)]">{index}</span>
          </motion.div>
        </div>

        {/* Right */}
        <div className="min-w-0">
          {desktopLayout === 'content-left' ? (
            <DesktopMeta align="start" />
          ) : (
            <DesktopContent />
          )}
        </div>
      </div>
    </motion.li>
  );
};
